    line-height: 2px;
    padding: 5px;
    font-size: 14px;
    color:#fafafa;
}
.pre-req-btn > button{
    background-color: #fafafa!important;
    color: #037ef3 !important;
    text-transform: none;
    left: 7px;
    font-weight: bold;
